.App {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.center {
    text-align: center;
}

.todoList-newTaskForm {
    margin: 10px;
}

.todoList-header_title {
}

.todoList-tasks {
    margin-bottom: 30px;
    margin-top: 5px;
}

.button {
    padding: 2px 10px;
    margin: 0 10px;
    border-radius: 4px;
}

.taskText {
    font-size: 1.3em;
    width: 200px;
    /*  text-transform: capitalize;*/
}

.buttonIntoTodoListFooter {

}

.todoList {
    width: 350px;
    margin: 10px auto;
    background-color: rgba(200, 197, 81, 0.49);
    border-radius: 10px;
}

.classNameForInput {
    border: 2px solid rgba(200, 197, 81, 0.49);
    padding: 5px 10px;
    background-color: whitesmoke;
    border-radius: 5px;
}

.todoList > div {
    padding: 10px;
}

.todoList-task {
    vertical-align: middle;
}

.priority {
    font-size: 0.7em;
}

.todoList-task.done {
    opacity: 0.4;
}

.filter-active {
    background-color: blue;
}

.error {
    border-color: red;
}

.filter-active {
    background-color: gold;
}
